<template>
  <div class="table">
    <!---->
    <div class="button">
        <ht-base-button @click="add('formValidate')" type="primary"></ht-base-button>
      <div class="search">
        <ht-base-input v-model="clientId" placeholder="ID" style="width:200px;margin-left: 5px" @keyup.enter.native="queryList()"/>
        <ht-base-input v-model="templateSign" placeholder="ID" style="width:200px;margin-left: 5px" @keyup.enter.native="queryList()"/>
        <ht-base-button type="info" @click="queryList()"></ht-base-button>
      </div>
    </div>

    <ht-base-modal v-model="addModal" :title="business" footer-hide>
      <ht-base-form class="modal-form" ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="140" style="height: 400px;">
        <ht-base-form-item label="" prop="name">
          <ht-base-input placeholder="" v-model="formValidate.name" style="width: 100%"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item label="ID" prop="clientId">
          <ht-base-input placeholder="ID" v-model="formValidate.clientId" style="width: 100%"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item label="SecretKey" prop="secretKey">
          <ht-base-input placeholder="SecretKey" v-model="formValidate.secretKey" style="width: 100%"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item label="Token" prop="baiduTokenUrl">
          <ht-base-input v-model="formValidate.baiduTokenUrl" placeholder="Token"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item label="ICOR" prop="baiduIocrUrl">
          <ht-base-input v-model="formValidate.baiduIocrUrl" placeholder="ICOR"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item label="ID" prop="templateSign">
          <ht-base-input v-model="formValidate.templateSign" placeholder="ID"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item class="modal-footer">
          <ht-base-button type="primary" ghost @click="cancel('formValidate')"></ht-base-button>
          <ht-base-button type="primary" @click="handleSubmit('formValidate')"></ht-base-button>
        </ht-base-form-item>
      </ht-base-form>
    </ht-base-modal>
    <!--table-->
    <vxe-grid
      border
      show-overflow
      show-header-overflow
      highlight-hover-row
      highlight-current-row
      ref="xGrid"
      :height="tableHeight"
      :data="tableData"
      :columns="tableColumn"
      :loading="loading"
      :start-index="(tablePage.currentPage - 1) * tablePage.pageSize"
      :pager-config="tablePage"
      @page-change="handlePageChange"
      :edit-config="{trigger: 'click'}">
      <template v-slot:empty>
            <span style="color: red;">
              <img src="../../../static/img/img1.gif">
              <p></p>
            </span>
      </template>
    </vxe-grid>
  </div>
</template>
<script>
    import {queryApi} from "../../service/api";

    export default {
        data() {
            return {
                tableHeight:'350',
                clientId: '',
                templateSign: '',
                addModal: false,
                business:'',
                formValidate: {
                  name: '',
                  clientId: '',
                  secretKey:'',
                  baiduTokenUrl:'',
                  baiduIocrUrl:'',
                  templateSign:''
                },
                ruleValidate: {
                    name:[
                      {required: true, message: '', trigger: 'blur'}
                    ],
                    clientId:[
                        {required: true, message: 'ID', trigger: 'blur'}
                    ],
                    secretKey:[
                        {required: true, message: 'SecretKey', trigger: 'blur'}
                    ],
                    baiduTokenUrl:[
                      {required: true, message: 'Token', trigger: 'blur'}
                    ],
                    baiduIocrUrl:[
                      {required: true, message: 'IOCR', trigger: 'blur'}
                    ],
                    templateSign:[
                      {required: true, message: 'ID', trigger: 'blur'}
                    ]
                },
                tableData: [],   //
                loading: false,   //
                tablePage: {     //
                    total: 0,     //
                    currentPage: 1,  //
                    pageSize: 10,   //
                    align: 'left',   //
                    pageSizes: [10, 20, 50, 100, 200, 500],  //
                    layouts: ['Sizes', 'PrevJump', 'PrevPage', 'Number', 'NextPage', 'NextJump', 'FullJump', 'Total'],   //
                    perfect: true   //
                },
                tableColumn: [     //table
                    {
                        field: 'id',
                        title: 'ID',
                        width: 80,
                        align: "center"
                    },
                    {
                      field: 'action',
                      title: '',
                      align: 'center',
                      width: 160,
                      slots: {
                        default: ({row, column}) => {
                          let value = row["userName"];
                          if(value == "admin"){
                            return [
                              <ht-base-button type = "primary" onClick={ () => this.edit(row, column) }></ht-base-button>
                            ]
                          }
                          else{
                            return [
                              <ht-base-button type = "primary" onClick={ () => this.edit(row, column) }></ht-base-button>,
                              <ht-base-button style="margin-left:10px" type="primary" ghost onClick={ () => this.remove(row, column) }></ht-base-button>
                            ]
                          }
                        }
                      }
                    },
                    {
                      field: 'name',
                      title: '',
                      width: 140,
                      align: "center",
                    },
                    {
                        field: 'clientId',
                        title: 'ID',
                        width: 140,
                        align: "center",
                    },
                    {
                      field: 'secretKey',
                      title: 'SecretKey',
                      width: 150,
                      align: "center",
                    },
                    {
                        field: 'baiduTokenUrl',
                        title: 'Token',
                        align: "left",
                        width: 180,
                    },
                    {
                        field: 'baiduIocrUrl',
                        title: 'IOCR',
                        align: "center",
                        width: 180,
                    },
                    {
                      field: 'templateSign',
                      title: 'ID',
                      align: "center",
                      width: 300,
                    },
                    {
                        field: 'isDelete',
                        title: '',
                        align: "center",
                        width:100,
                        slots: {
                            default: ({row, column}) => {
                                if (row.isDelete == 0) {
                                    return [
                                        <div></div>
                                    ]
                                } else{
                                    return [
                                        <div style="color:red;"></div>
                                    ]
                                }
                            }
                        }
                    },
                    {
                        field: 'createDate',
                        title: '',
                        align: "center",
                        width:150,
                        slots: {
                            default: ({row, column}) => {
                               if(row.createDate != null){
                                   return row.createDate.substring(0, 10)
                               }
                            }
                        }
                    },
                    {
                      field: 'modifyDate',
                      title: '',
                      align: "center",
                      width:150,
                      slots: {
                        default: ({row, column}) => {
                          if(row.modifyDate != null){
                            return row.modifyDate.substring(0, 10)
                          }
                        }
                      }
                    }
                ],
            }
        },
        created() {
            this.tableHeight = window.innerHeight - 180;
            this.findList();  //table
        },
        methods: {
            //table
            findList() {
              this.loading = true;
                queryApi('api/config/getConfigList', {
                    pageNo: this.tablePage.currentPage,
                    pageSize: this.tablePage.pageSize,
                    clientId: this.clientId,
                    templateSign: this.templateSign
                }).then((res) => {
                  if (res.total === 0 || res.total === null) {
                    this.tableData = []
                    this.tablePage.total = 0;
                    this.tablePage.currentPage = 1;
                  } else {
                    this.tablePage.total = res.total;
                    this.tableData = res.list;
                  }
                  this.loading = false;
                });
            },

            //
            handlePageChange({currentPage, pageSize}) {
                this.tablePage.currentPage = currentPage;
                this.tablePage.pageSize = pageSize;
                this.findList()
            },
            //
            queryList() {
                this.tablePage.currentPage=1;
                this.findList();
            },
            //
            add(name) {
                this.addModal = true;
                this.formValidate.id = '';
                this.formValidate.name = '';
                this.formValidate.clientId = '';
                this.formValidate.secretKey = '';
                this.formValidate.baiduTokenUrl = '';
                this.formValidate.baiduIocrUrl='';
                this.formValidate.templateSign = '';
                this.business="";
                this.$refs[name].resetFields();
            },
            handleSubmit(name) {
                if (this.formValidate.id != "") {
                    this.$refs[name].validate((valid) => {
                        if (valid) {
                            queryApi('api/config/editConfig', (this.formValidate)).then((res) => {
                                if (res !== undefined) {
                                    this.addModal = false;
                                    this.$refs[name].resetFields();
                                    this.$HtBaseMessage.success('!');
                                    this.findList();
                                } else {
                                    this.$HtBaseMessage.error('!');
                                }
                            });
                        }
                    })
                } else {
                    this.$refs[name].validate((valid) => {
                        if (valid) {
                            queryApi('api/config/addConfig', (this.formValidate)).then((res) => {
                                if (res !== undefined) {
                                    this.$refs[name].resetFields();
                                    this.$HtBaseMessage.success('!');
                                    this.addModal = false;
                                    this.findList();
                                } else {
                                    this.$HtBaseMessage.error('!');
                                }
                            });
                        }
                    })
                }
            },
            //modal
            cancel(name) {
                this.$refs[name].resetFields();
                this.addModal = false;
            },
            //
            edit(row) {
                this.$refs['formValidate'].resetFields();
                this.addModal = true;
                this.business="";
                this.formValidate.id = row.id;
                queryApi('api/config/queryById', {
                    id: this.formValidate.id,
                }).then((res) => {
                    if (res !== false) {
                        this.formValidate.id = this.formValidate.id;
                        this.formValidate.clientId = res.clientId;
                        this.formValidate.name = res.name;
                        this.formValidate.secretKey = res.secretKey;
                        this.formValidate.baiduTokenUrl = res.baiduTokenUrl;
                        this.formValidate.baiduIocrUrl = res.baiduIocrUrl;
                        this.formValidate.templateSign = res.templateSign;
                    }
                });
            },

            //
            remove(row) {
                let that=this;
                var ids = [];
                ids.push(row.id);
                that.$HtBaseModal.confirm({
                    title: "",
                    onOk() {
                        queryApi('api/config/deleteById', {
                            ids: JSON.parse(ids),
                        }).then((res) => {
                            if (res != false) {
                              that.findList();
                              this.$HtBaseMessage.success('!');
                            }
                        });
                    },
                    cancelText: '',
                });
            }
        }
    }
</script>

<style scoped xml:lang="less">
  .modal-form{
    height: 350px;
  }
  .table {
    overflow: scroll;
  }

  .search {
    float: right;
    margin-right: 10px;
  }

  .button {
    width: 100%;
    display: block;
    padding: 17px 0;
  }
</style>



