<template>
  <ht-base-layout>
    <ht-base-header>
      <div class="header-logo">V1.0.0</div>
      <ht-base-menu class="header-nav" mode="horizontal" theme="primary">
        <ht-base-menu-item v-for="(item,index) in tabListHeader" :name="item.name" :key="index">
          <router-link :to="item.path">
            {{item.title}}
          </router-link>
        </ht-base-menu-item>
      </ht-base-menu>
      <ht-base-dropdown trigger="click" class="header-user" @on-click="information">
        <div class="header-user-in">
          <ht-base-avatar src="https://i.loli.net/2017/08/21/599a521472424.jpg" size="large" style="margin-right: 8px"/>
          {{this.userInfo.userName}}
        </div>
        <ht-base-dropdown-menu slot="list">
          <ht-base-dropdown-item name="1"></ht-base-dropdown-item>
          <ht-base-dropdown-item name="2"></ht-base-dropdown-item>
        </ht-base-dropdown-menu>
      </ht-base-dropdown>
    </ht-base-header>
    <ht-base-content>
      <div class="content-left">
        <ht-base-menu mode="vertical" theme="light" :active-name="activeName"
                      style="width:256px;min-height: 100%">
          <ht-base-menu-item v-for="(item,index) in tabList" :name="item.name" :key="index">
            <router-link :to="item.path">
              <ht-base-icon :type='item.icon' size=14 style="margin-right: 10px"/>
              {{item.title}}
            </router-link>
          </ht-base-menu-item>
        </ht-base-menu>
      </div>
      <div class="content-right">
        <ht-base-breadcrumb>
          
          <ht-base-breadcrumb-item></ht-base-breadcrumb-item>
          <ht-base-breadcrumb-item>{{this.breakName}}</ht-base-breadcrumb-item>
        </ht-base-breadcrumb>
        <router-view id="myrame"></router-view>
      </div>
    </ht-base-content>
    <!---->
    <ht-base-modal v-model="userInfoForm" title="" footer-hide>
      <ht-base-form :label-width="100">
        <ht-base-form-item label=":">
          <ht-base-avatar src="https://i.loli.net/2017/08/21/599a521472424.jpg" size="large"/>
          <span style="display: inline-block;margin-left: 20px;"><a @click="showModifyPwdForm" target="_blank"></a></span>
        </ht-base-form-item>
        <ht-base-form-item label=":">
          <div>{{this.userInfo.userName}}</div>
        </ht-base-form-item>
        <ht-base-form-item label=":">
          <div>{{this.userInfo.roleName}}</div>
        </ht-base-form-item>
        <ht-base-form-item label=":">
          <div>{{this.userInfo.linkMobile}}</div>
        </ht-base-form-item>
        <ht-base-form-item label=":">
          <div>{{this.userInfo.email}}</div>
        </ht-base-form-item>
      </ht-base-form>
    </ht-base-modal>
    <!---->
    <ht-base-modal v-model="modifyPwdForm" title="" footer-hide>
      <ht-base-form class="modal-form" ref="formCustoms" :model="formCustoms" :rules="ruleCustoms" :label-width="80">
        <ht-base-form-item label="" prop="passwd">
          <ht-base-input type="password" v-model="formCustoms.passwd" placeholder=""></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item label="" prop="passwdCheck">
          <ht-base-input type="password" v-model="formCustoms.passwdCheck" placeholder=""></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item class="beforeBut modal-footer">
          <ht-base-button type="primary" ghost @click="hideModifyPwdForm('formCustoms')"></ht-base-button>
          <ht-base-button type="primary" @click="modifyPwd('formCustoms')"></ht-base-button>
        </ht-base-form-item>
      </ht-base-form>
    </ht-base-modal>
    <!---->
    <ht-base-modal v-model="configForm" title="" footer-hide>
      <ht-base-form class="modal-form" ref="configInfo" :model="configInfo" :label-width="150">
        <ht-base-form-item label="Token" prop="baiduTokenUrl">
          <ht-base-input v-model="configInfo.baiduTokenUrl" placeholder="Token"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item label="IOCR" prop="baiduIocrUrl">
          <ht-base-input v-model="configInfo.baiduIocrUrl" placeholder="IOCR"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item label="API Key" prop="clientId">
          <ht-base-input v-model="configInfo.clientId" placeholder="API Key"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item label="Secret Key" prop="secretKey">
          <ht-base-input v-model="configInfo.secretKey" placeholder="Secret Key"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item label="ID" prop="templateSign">
          <ht-base-input v-model="configInfo.templateSign" placeholder="ID"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item label="ID" prop="templateSign">
          <ht-base-input v-model="configInfo.templateSign" placeholder="ID"></ht-base-input>
        </ht-base-form-item>
        <ht-base-form-item class="modal-footer">
          <ht-base-button type="primary" ghost @click="hideConfigForm('formCustoms')"></ht-base-button>
          <ht-base-button type="primary" @click="changeConfigInfo('formCustoms')"></ht-base-button>
        </ht-base-form-item>
      </ht-base-form>
    </ht-base-modal>
  </ht-base-layout>
</template>

<script>
    import '../../themes/default/custom.less'
    import {queryApi} from "../../service/api"
    import {Notice} from 'iview'

    export default {
        data() {
            const validatePasss = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else {
                    if (this.formCustoms.passwdCheck !== '') {
                        // 
                        this.$refs.formCustoms.validateField('passwdCheck');
                    }
                    callback();
                }
            };
            const validatePassChecks = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else if (value !== this.formCustoms.passwd) {
                    callback(new Error(''));
                } else {
                    callback();
                }
            };
            return {
                isAdmin: false,
                userInfoForm: false, //
                modifyPwdForm: false,
                configForm : false,
                activeNameHeader: 'menu1',    //
                userInfo: {},     //
                configInfo: {},
                activeName: '1',    //
                tabList: [         //
                    {
                        name: "commodityIndex",
                        icon: "LiRa_edit1",
                        title: "",
                        path: '/commodityIndex'
                    }
                ],
                tabListHeader : [
                    {
                        name: "commodityIndex",
                        title: "",
                        path: '/commodityIndex'
                    }
                ],
                breakName: "",  //
                configModals: false,
                formCustoms: {
                    passwd: '',
                    passwdCheck: ''
                },
                ruleCustoms: {
                    passwd: [
                        {required: true, validator: validatePasss, trigger: 'blur'},
                        {
                            pattern: /^(?=.*[0-9])(?=.*[a-z])(?=.*[^a-zA-Z0-9]).{8,16}$/,
                            message: '8-16'
                        }
                    ],
                    passwdCheck: [
                        {required: true, validator: validatePassChecks, trigger: 'blur'}
                    ]
                },
                roleId:'',    //
            }
        },
        created(){
            //
            if (this.$route.meta.menuFocus) {
                this.activeNameHeader = this.$route.meta.menuFocus
                this.activeName = this.$route.meta.menuSecondFocus
                this.breakName = this.$route.meta.menuTitle
            }
        },
        watch: {
            '$route.path': function (newVal, oldVal) {
                //meta
                if (this.$route.meta.menuFocus) {
                    this.activeNameHeader = this.$route.meta.menuFocus
                    this.$nextTick(()=>{
                        this.activeName = this.$route.meta.menuSecondFocus
                        this.breakName = this.$route.meta.menuTitle
                    })
                }
            },
            activeNameHeader(newVal,oldVal){
            },
            $route: {
                // valoldValval
                handler: function(val, oldVal){
                    if(this.roleId == 0){
                        if(val.path =='/userIndex'){
                            window.location.href = '../../../static/login/403.html'
                        }
                    }
                },
                // 
                deep: true
            }
        },
        mounted() {
            this.getUserInfo();
        },
        methods: {
            getUserInfo() {
                queryApi('api/user/getUserInfo', {}).then((res) => {
                    if (res !== undefined) {
                        this.userInfo.userName = res.userName;
                        this.userInfo.linkMobile = res.linkMobile;
                        this.userInfo.email = res.email;
                        this.roleId = res.roleId;

                      // res.roleId = 1;

                        if(res.roleId == 1){
                            this.userInfo.roleName = '';
                        }
                        else{
                            this.userInfo.roleName = '';
                        }

                        if (res.roleId == 1) {
                            this.isAdmin = true;
                            this.tabList = [
                                {
                                    name: "commodityIndex",
                                    icon: "LiRa_edit1",
                                    title: "",
                                    path: '/commodityIndex'
                                },
                                {
                                    name: "userIndex",
                                    icon: "LiRa_edit1",
                                    title: "",
                                    path: '/userIndex'
                                },
                                {
                                  name: "configIndex",
                                  icon: "LiRa_edit1",
                                  title: "",
                                  path: '/configIndex'
                                }
                            ]
                        } else {
                            this.tabListHeader = [
                                {
                                    name: "commodityIndex",
                                    title: "",
                                    path: '/commodityIndex'
                                }
                            ]
                        }
                    }
                })
            },
            //
            information(name) {
                if (name == 1) {
                    this.getUserInfo();
                    this.userInfoForm = true;
                }
                else if (name == 2) {
                    this.$HtBaseModal.confirm({
                        title: "",
                        onOk() {
                            queryApi('api/logout', {}).then((res) => {
                                window.location.href = '../../../static/login/index.html'
                            });
                        },
                        cancelText: '',
                    });
                }
            },
            // 
            modifyPwd(name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        queryApi('api/user/modifyPwd', {
                            password: this.formCustoms.passwdCheck
                        }).then((res) => {
                            if (res !== undefined) {
                                Notice.success({
                                    title: '',
                                    desc: ''
                                });
                                setTimeout(function(){
                                    window.location.href = '../../../static/login/index.html'
                                }, 1500);
                            }
                        });
                    }
                })
            },
            showModifyPwdForm() {
                this.modifyPwdForm = true;
            },
            hideModifyPwdForm() {
                this.modifyPwdForm = false;
            }
        }
    }
</script>

<style scoped xml:lang="less">
  .modal-form .beforeBut {
    position: absolute;
    height: 72px;
    width: 100%;
    left: 0;
    bottom: -82px;
    padding: 20px;
    background: #fff;
    border-radius: 0 0 6px 6px;
  }
</style>



